import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';

const DirectoryView = props => (
    <section>
      <div id={'search'}>
        <SearchForm searchVal={props.searchVal} clickHandler={props.clickHandler} chooseRecipe={props.chooseRecipe} />
        <ul id={'results'}>
          {props.selectedRecipes.map(recipe => (
            <RecipeCard name={recipe.name} id={recipe.id} selectDetail={props.selectDetail} />
          ))}
        </ul>
      </div>
      <RecipeIngredients recipeDetails={props.recipeDetails}/>
    </section>
  );
  
  const SearchForm = (props) => (
    <form>
      <input value={props.searchVal} onChange={props.clickHandler} />
      <button onClick={props.chooseRecipe}>Search</button>
    </form>
  );

  const RecipeCard = (props) => (
    <li className={'card'} onClick={(e)=>props.selectDetail(e,props.id)}>
      <p>{props.name}</p>
    </li>
  );
  

const RecipeIngredients = (props) => (
    <article>
      <h1>{props.recipeDetails.name.toUpperCase()}</h1>
      <div className={'details'}>
        <div id={'ingredients'}>
          <h3>INGREDIENTS</h3>
          <ul>{props.recipeDetails.ingredients.map(ingredient => (
              <li> <p>{ingredient}</p></li>
            ))}</ul>
        </div>

        <div id={'instructions'}>
          <h3>INSTRUCTIONS</h3>
          <ul>{props.recipeDetails.instructions.map(instruction => (
                <li> <p>{instruction}</p></li>
            ))}</ul></div>
      </div>
    </article>
  );

class App extends React.Component{
    state = {
        recipes: [ 
       { 
        id: 1,
        name: 'shortbread cookie',
        ingredients: ['dough', 'sugar', 'whipped cream'],
        instructions: ['pre-heat over to 350', 'tickle the cookie', 'laugh']
        },
        {
        id: 2,
        name: 'cheesecake',
        ingredients: ['cream cheese', 'cakemix', 'sugar'],
        instructions: ['pre-heat over to 350', 'tickle the cheese', 'cry']
        },
        {
        id: 3,
        name: 'chocolate chip cookies',
        ingredients: ['chocolate', 'chip', 'dough'],
        instructions: ['pre-heat over to 350', 'tickle the chocolate', 'mix']
        },
        {
        id: 4,
        name: 'cinammon rolls',
        ingredients: ['cinammon powder', 'dough', 'sugar',],
        instructions: ['sprinkle cinnamon powder on the dough', 'heat', 'add sugar']
        },
        {
        id: 5,
        name: 'apple pie',
        ingredients: ['apple', 'pie mix', 'sugar'],
        instructions: ['pre-heat over to 350', 'dice apple', 'crush apple pieces', 'blend apple pieces', 'add pie mix', 'smooth in pan', 'put in oven for 60 minutes']
        },
        {
        id: 6,
        name: 'banana pudding',
        ingredients: ['bananas', 'pudding mix', 'sugar'],
        instructions: ['mix bananas with pudding', 'add sugar', 'stir well']
        },
        {
        id: 7,
        name: 'chocolate mousse',
        ingredients: ['chocolate mix', 'mouse', 'cake mix'],
        instructions: ['pre-heat over to 350', 'mix chocolate in bowl', 'pour in mousse']
        },
        {
        id: 8,
        name: 'peach cobbler',
        ingredients: ['diced peaches', 'honey'],
        instructions: ['put diced peaches in a bowl', 'pour honey in bowl', 'mix to perfection']
        },
        {
        id: 9,
        name: 'brownies',
        ingredients: ['brownie mix', 'water', 'sugar'],
        instructions: ['put brownie mix in bowl', 'sprinkle sugar', 'put in oven for 45 mins']
        }
        
    ],
    
    searchVal: '',
    selectedRecipes: [],
    recipeDetails: {
      id: -1,
      name: "",
      ingredients: [],
      instructions: []
    }
  };

  clickHandler = (e) => {
    e.preventDefault();
    this.setState({ searchVal: e.target.value});
  }

  chooseRecipe = (e) => {
    e.preventDefault();
    const selects = this.state.recipes.filter(recipe => recipe.name.includes(this.state.searchVal));
    this.setState({selectedRecipes: selects});
  }

  selectDetail = (e, id) => {
    e.preventDefault();
    const recipe = this.state.recipes.find(recipe => recipe.id === id)
    this.setState({recipeDetails: recipe})
  }

  componentDidMount() {
    this.setState({selectedRecipes: this.state.recipes });
    this.setState({recipeDetails: this.state.recipes[0]})
  }

  render() {
    return (
      <DirectoryView
        selectedRecipes={this.state.selectedRecipes}
        searchVal={this.state.searchVal}
        recipeDetails={this.state.recipeDetails}
        clickHandler={this.clickHandler}
        chooseRecipe={this.chooseRecipe}
        selectDetail={this.selectDetail}
      />
    );
  }
}

ReactDOM.render(<App />, document.getElementById("root"));